
.App-header {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #000000;
}

@media (max-width:768px){
  body{
    background-image: url("https://raw.githubusercontent.com/rajesh-govada/images/assets/mob.jpg");
    background-repeat:no-repeat; 
    background-size: cover;
    background-position: top center;
    color: #000000;  
  };
}

@media (min-width:769px){
  body{
    background-image: url("https://raw.githubusercontent.com/rajesh-govada/images/assets/pc.jpg");
    background-repeat:no-repeat;   
    background-size: cover;
    background-position: top center;  
  };
}

.App-link {
  color: #0f0f0e;
}

